import React from "react";
import { useAuth0 } from "@auth0/auth0-react";

const LogoutButton = () => {
  const { logout } = useAuth0();
  const returnTo = window.location.origin + "/Stoxy";
  return (
    <button onClick={() => logout({ logoutParams: { returnTo: returnTo  } })}>
      Log Out
    </button>
  );
};

export default LogoutButton;